import { baseApiUrl } from '@/global'
import axios from 'axios'

export default {
  name: 'ObraById',
  data() {
    return {
      obras: {},
      capitulos: [],
      universos: {},
      avisos: [],
      caracteristicas: {},
    }
  },

  methods: {
    getObras() {
      const url = ` ${baseApiUrl}/obra/${this.$route.params.obraId}`
      axios.get(url).then((res) => (this.obras = res.data))
    },

    getUniversos() {
      const url = ` ${baseApiUrl}/obra/${this.$route.params.obraId}/universos`
      axios.get(url).then((res) => (this.universos = res.data))
    },

    getAvisos() {
      const url = ` ${baseApiUrl}/obra/${this.$route.params.obraId}/avisos`
      axios.get(url).then((res) => (this.avisos = res.data))
    },

    getCaracteristicas() {
      const url = ` ${baseApiUrl}/obra/${this.$route.params.obraId}/caracteristicas`
      axios.get(url).then((res) => (this.caracteristicas = res.data))
    },

    getCapitulosByObra() {
      const url = ` ${baseApiUrl}/obra/${this.$route.params.obraId}/capitulos`
      axios.get(url).then((res) => (this.capitulos = res.data))
    },
  },

  computed: {
    classificacao() {
      let imagemClass = ''
      if (this.obras.classificacao === 'livre') return (imagemClass = 'livre.png')
      if (this.obras.classificacao === '10+') return (imagemClass = '10-anos.png')
      if (this.obras.classificacao === '12+') return (imagemClass = '12-anos.png')
      if (this.obras.classificacao === '14+') return (imagemClass = '14-anos.png')
      if (this.obras.classificacao === '16+') return (imagemClass = '16-anos.png')
      if (this.obras.classificacao === '18+') return (imagemClass = '18-anos.png')

      return imagemClass
    },
  },

  mounted() {
    this.getObras()
    this.getCapitulosByObra()
    this.getUniversos()
    this.getAvisos()
    this.getCaracteristicas()
  },
}
