import { baseApiUrl, showError } from '../../global'
import axios from 'axios'
export default {
  data() {
    return {
      show1: false,
      usuario: {},
    }
  },

  methods: {
    getUserByToken() {
      const url = ` ${baseApiUrl}/token/${this.$route.params.token}`
      axios
        .get(url)
        .then((res) => {
          this.usuario = res.data
        })
        .catch(showError)
    },

    resetPassword() {
      const url = ` ${baseApiUrl}/usuario/${this.$route.params.token}/resetPassword `
      axios
        .put(url, {
          password: this.usuario.newPassword,
        })
        .then(() => {
          this.$toast.success('Senha alterada com sucesso')
        })
        .catch(showError)
    },

    passwordConfirmationRule() {
      return this.usuario.newPassword === this.usuario.confirmarPassword || 'Senhas no conferem'
    },

    telaLogin() {
      this.$router.push({ path: `/login/` })
    },
  },

  mounted() {
    this.getUserByToken()
  },
}
